<script lang="ts" setup>
  import { reactive, onMounted, ref, toRaw, onActivated } from 'vue';
  import { useI18n } from '@/hooks/web/useI18n';
  import { useMessage } from '@/hooks/web/useMessage';
  import { IconEnum } from '@/enums/appEnum';
  import { BasicTable, useTable, SorterResult } from '@/components/Table';
  import { TreeItem } from '/@/components/Tree';
  import { PageWrapper } from '/@/components/Page';
  import CommonTree from '@/components/Framework/Tree/CommonTree.vue';
  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';
  import * as ProcessInstanceApi from '@/api/bpm/processInstance';

  import * as WarehousingApi from '@/api/po/equipment/warehousing';
  import * as WarehousingDtlApi from '@/api/po/equipment/warehousing/dtl';
  import * as CommonApi from '@/api/baseset/common';
  import * as InventoryApi from '@/api/po/equipment/inventory';
  import { exportExcelFile } from '@/utils/file/download';

  import { mainColumns, detailColumns } from './warehousing.data';
  import { addTabPage } from '@/utils/route';
  import { MsgManager } from '/@/message/MsgManager';
  import * as CommonUtil from '/@/utils/common';
  import { assign } from 'lodash-es';
  import { DownOutlined, UpOutlined } from '@ant-design/icons-vue';

  defineOptions({ name: 'Warehousing' });

  const { t } = useI18n();
  const message = useMessage();

  const treeData = ref<TreeItem[]>([]); // 左侧电站树数据
  const selectedNode = ref({}); // 被选择的电站节点
  const expand = ref(false);

  // 左侧树状菜单选中事件
  function handleSelect(node = {}) {
    selectedNode.value.stationOrganId = node.organId;
    selectedNode.value.stationOrganName = node.organName;
    selectedNode.value.stationId = node.powerStationId;
    selectedNode.value.stationName = node.powerStationName;
    selectedNode.value.warehouseId = null;
    selectedNode.value.warehouseName = null;
    selectedNode.value.warehouseType = null;
    if (node.nodeKindId == 'Warehouse') {
      if (node.status == 0) {
        return;
      }
      selectedNode.value.warehouseId = node.id;
      selectedNode.value.warehouseName = node.name;
      selectedNode.value.warehouseType = node.stationType;
    }

    search();
  }

  // 查询左侧电站树数据
  async function queryDeptTreeList() {
    const deptList = (await CommonApi.getOrgStationWarehouseTree()) as unknown as TreeItem[];
    treeData.value = deptList;
  }

  const activeKey = ref('1');

  const formRef = ref();
  const formState = reactive({
    billCode: '',
    warehousingType: '',
    warehousingDate: [],
    materialCode: '',
    materialName: '',
    status: '',
  });

  const search = () => {
    reloadMain();
    reloadDetail();
  };
  const reset = () => {
    formState.billCode = '';
    formState.warehousingType = '';
    formState.warehousingDate = [];
    formState.materialCode = '';
    formState.materialName = '';
    formState.status = '';
    reloadMain();
    reloadDetail();
  };

  //==================================入库记录  begin ==================================

  const getIsSelectData = () => {
    return getSelectRows().length == 0;
  }

  const searchInfo = reactive<any>({});
  const [
    registerMainTable,
    { getForm: getMainForm, reload: reloadMain, getSelectRows, clearSelectedRowKeys, redoHeight },
  ] = useTable({
    api: WarehousingApi.getEquipmentWarehousingPage,

    target: '#warehousing-tab-content',
    targetTabKey: '1',
    targetTabValue: activeKey,

    columns: mainColumns,
    useSearchForm: false,
    bordered: true,
    rowSelection: {
      type: 'radio',
    },
    beforeFetch(search) {
      search.stationOrganId = selectedNode.value.stationOrganId;
      search.stationId = selectedNode.value.stationId;
      search.warehouseId = selectedNode.value.warehouseId;

      search.billCode = formState.billCode;
      search.warehousingType = formState.warehousingType;

      if (formState.warehousingDate != null && formState.warehousingDate.length > 0) {
        search.startDate = formState.warehousingDate[0];
        search.endDate = formState.warehousingDate[1];
      }

      search.materialCode = formState.materialCode;
      search.materialName = formState.materialName;
      search.status = formState.status;

      assign(search, searchInfo);

      return search;
    },
    sortFn: (sortInfo: SorterResult) => {
      const sortOrder = sortInfo.order?.replace('end', '');
      searchInfo.sortOrder = sortOrder;
      searchInfo.sortField = sortOrder ? CommonUtil.toUnderline(sortInfo.field) : undefined;
    },
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    showIndexColumn: true,
  });

  async function handleCreate() {
    if (selectedNode.value.warehouseType == null) {
      message.warning('请选择库房。');
      return;
    }
    const res = await InventoryApi.getInventoryProcessExists(selectedNode.value.warehouseId);
    if (res) {
      message.error('当前有在途（审批中）盘点单，请完成后再入库。');
      return;
    }
    const formData = toRaw(selectedNode.value);
    addTabPage('/po/equipment/warehousing/create', getTabPageName(), formData);
  }

  function checkSelectData() {
    const rows = getSelectRows();
    if (rows.length == 0) {
      message.warning('请选择一行数据');
      return false;
    }
    return rows[0];
  }

  const handleDelete = async () => {
    const record = checkSelectData();
    if (!record) {
      return;
    }
    await message.delConfirm();
    await ProcessInstanceApi.deleteProcessInstance(record.processInstanceId);
    message.success(t('common.delSuccess'));
    clearSelectedRowKeys();
    reloadMain();
  };

  /** 审批进度 */
  function handleView(record: any) {
    addTabPage(
      `/po/equipment/warehousing/create?processInstanceId=${record.processInstanceId}`,
      getTabPageName(record),
    );
  }

  const tableTitle = ref<string>('设备入库');
  const getTabPageName = (record?: any) => {
    const name = tableTitle.value;
    if (!record?.id || record?.status === 0) {
      return `${t('common.action.create')}${name}`;
    }
    if (record?.status === 1 || record?.status === 6) {
      return `审核${name}`;
    }
    return `查看${name}`;
  };

  async function handleMainExport() {
    message.createConfirm({
      title: t('common.exportTitle'),
      iconType: 'warning',
      content: t('common.exportMessage'),
      async onOk() {
        let params = getMainForm().getFieldsValue() || {};
        params.stationOrganId = selectedNode.value.stationOrganId;
        params.stationId = selectedNode.value.stationId;
        params.warehouseId = selectedNode.value.warehouseId;
        await WarehousingApi.exportEquipmentWarehousing(params).then((res) => {
          exportExcelFile(res?.data, '入库记录.xls');
        });
        message.success(t('common.exportSuccessText'));
      },
    });
  }

  //==================================入库记录  end ==================================

  //==================================入库明细  begin ==================================
  const detailSearchInfo = reactive<any>({});
  const [
    registerDetailTable,
    { getForm: getDetailForm, reload: reloadDetail, redoHeight: redoHeight_ },
  ] = useTable({
    api: WarehousingDtlApi.getEquipmentWarehousingDtlPage,

    target: '#warehousing-tab-content',
    targetTabKey: '2',
    targetTabValue: activeKey,

    columns: detailColumns,
    useSearchForm: false,
    bordered: true,
    beforeFetch(search) {
      search.stationOrganId = selectedNode.value.stationOrganId;
      search.stationId = selectedNode.value.stationId;
      search.warehouseId = selectedNode.value.warehouseId;

      search.billCode = formState.billCode;
      search.warehousingType = formState.warehousingType;

      if (formState.warehousingDate != null && formState.warehousingDate.length > 0) {
        search.startDate = formState.warehousingDate[0];
        search.endDate = formState.warehousingDate[1];
      }

      search.materialCode = formState.materialCode;
      search.materialName = formState.materialName;
      search.status = formState.status;

      assign(search, detailSearchInfo);

      return search;
    },
    sortFn: (sortInfo: SorterResult) => {
      const sortOrder = sortInfo.order?.replace('end', '');
      detailSearchInfo.sortOrder = sortOrder;
      detailSearchInfo.sortField = sortOrder ? CommonUtil.toUnderline(sortInfo.field) : undefined;
    },
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    showIndexColumn: true,
  });

  async function handleDetailExport() {
    message.createConfirm({
      title: t('common.exportTitle'),
      iconType: 'warning',
      content: t('common.exportMessage'),
      async onOk() {
        let params = getDetailForm().getFieldsValue() || {};
        params.stationOrganId = selectedNode.value.stationOrganId;
        params.stationId = selectedNode.value.stationId;
        params.warehouseId = selectedNode.value.warehouseId;
        await WarehousingDtlApi.exportEquipmentWarehousingDtl(params).then((res) => {
          exportExcelFile(res?.data, '入库明细.xls');
        });
        message.success(t('common.exportSuccessText'));
      },
    });
  }

  //==================================入库明细  end ==================================

  const handleTabChange = () => {
    redoHeight();
    redoHeight_();
  };

  const handleFormToggle = () => {
    expand.value = !expand.value;
    setTimeout(handleTabChange, 300);
  };

  onActivated(() => {
    handleTabChange();
  });

  onMounted(() => {
    queryDeptTreeList();
    MsgManager.getInstance().listen('warehousing-page', reloadMain);
  });
</script>
<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <!-- 电站组织树 -->
    <CommonTree
      title="电站组织"
      @select="handleSelect"
      :value="treeData"
      class="left-tree-layout"
      :toolbar="false"
      :fieldNames="{ key: 'id', title: 'name' }"
    />

    <div class="right-table-layout tab-model">
      <div class="tab-button-div">
        <a-form class="search-form-box" ref="formRef" :model="formState" autocomplete="off">
          <a-row :gutter="32">
            <a-col flex="27.5%">
              <a-form-item
                name="billCode"
                label="入库批次"
                :labelCol="{ style: { width: '120px' } }"
                :colon="false"
              >
                <a-input v-model:value="formState.billCode" style="width: 100%" />
              </a-form-item>
            </a-col>
            <a-col flex="27.5%">
              <a-form-item
                name="warehouseType"
                label="入库类型"
                :labelCol="{ style: { width: '120px' } }"
                :colon="false"
              >
                <DictSelectBox
                  :type="`warehousing_type`"
                  @change="`()=> formState.warehousingType = value`"
                  v-model:value="formState.warehousingType"
                  style="width: 100%"
                />
              </a-form-item>
            </a-col>
            <a-col flex="27.5%">
              <a-form-item
                name="warehousingDate"
                label="入库日期"
                :labelCol="{ style: { width: '120px' } }"
                :colon="false"
              >
                <a-range-picker
                  v-model:value="formState.warehousingDate"
                  value-format="YYYY-MM-DD"
                  style="width: 100%"
                />
              </a-form-item>
            </a-col>
            <a-col flex="17.5%">
              <a-form-item style="text-align: right">
                <a-button class="tab-button" @click="reset">{{ t('common.resetText') }}</a-button>
                <a-button type="primary" class="tab-button" @click="search">{{
                  t('common.queryText')
                }}</a-button>
                <a style="font-size: 12px" @click="handleFormToggle">
                  <template v-if="expand">
                    收起
                    <UpOutlined />
                  </template>
                  <template v-else>
                    展开
                    <DownOutlined />
                  </template>
                </a>
              </a-form-item>
            </a-col>
          </a-row>
          <a-row v-show="expand" :gutter="32">
            <a-col flex="27.5%">
              <a-form-item
                name="materialCode"
                label="物料编码"
                :labelCol="{ style: { width: '120px' } }"
                :colon="false"
              >
                <a-input v-model:value="formState.materialCode" style="width: 100%" />
              </a-form-item>
            </a-col>
            <a-col flex="27.5%">
              <a-form-item
                name="materialName"
                label="物料"
                :labelCol="{ style: { width: '120px' } }"
                :colon="false"
              >
                <a-input v-model:value="formState.materialName" style="width: 100%" />
              </a-form-item>
            </a-col>
            <a-col flex="27.5%">
              <a-form-item
                name="status"
                label="状态"
                :labelCol="{ style: { width: '120px' } }"
                :colon="false"
              >
                <DictSelectBox
                  :type="`bpm_process_instance_result`"
                  @change="`()=> formState.status = value`"
                  v-model:value="formState.status"
                  style="width: 100%"
                />
              </a-form-item>
            </a-col>
          </a-row>
        </a-form>
      </div>
      <div id="warehousing-tab-content" class="tab-table-div tab-table-content">
        <a-tabs v-model:activeKey="activeKey" @change="handleTabChange">
          <a-tab-pane key="1" tab="入库记录">
            <BasicTable
              @register="registerMainTable"
              class="fix-basic-table"
              @resize-column="(w, col) => (col.width = w)"
              @row-db-click="(record) => CommonUtil.toFlowPage(record.id)"
            >
              <template #toolbar>
                <a-button type="primary" :preIcon="IconEnum.ADD" @click="handleCreate">{{
                  t('common.action.create')
                }}</a-button>
                <a-button class="red-btn" :disabled="getIsSelectData()" :preIcon="IconEnum.DELETE" @click="handleDelete">{{
                  t('common.delText')
                }}</a-button>
                <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleMainExport">{{
                  t('common.action.export')
                }}</a-button>
              </template>
            </BasicTable>
          </a-tab-pane>
          <a-tab-pane key="2" tab="入库明细">
            <BasicTable
              @register="registerDetailTable"
              class="fix-basic-table"
              @resize-column="(w, col) => (col.width = w)"
            >
              <template #toolbar>
                <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleDetailExport">
                  {{ t('common.exportTitle') }}
                </a-button>
              </template>
            </BasicTable>
          </a-tab-pane>
        </a-tabs>
      </div>
    </div>
  </PageWrapper>
</template>
<style lang="less" scoped>
  :deep(.vben-basic-table-header__toolbar) {
    margin-top: 8px;
  }

  .tabs-extra-demo-button {
    margin-right: 16px;
  }

  .left-tree-layout {
    width: 298px;
    border-radius: 2px;
    flex-shrink: 0;
  }

  .right-table-layout {
    width: calc(100% - 308px);
  }

  .tab-model {
    height: 100vh;
    padding: 10px 10px 0;

    div.tab-table-content {
      :deep(.vben-basic-table) {
        max-width: 100%;
        height: 100%;
        margin-top: -12px;
      }
    }
  }

  .tab-button-div {
    padding: 12px 16px 4px;
    :deep(.ant-form-item) {
      margin-bottom: 8px;
    }
  }

  .tab-button {
    margin-right: 10px;
  }

  .tab-table-div {
    margin-top: 10px;
    margin-bottom: 10px;
    overflow: auto;
    border-radius: 2px;

    :deep(.ant-tabs-top > .ant-tabs-nav) {
      margin-bottom: 10px;
    }
  }

  .ant-row-rtl .tabs-extra-demo-button {
    margin-right: 5px;
    margin-left: 16px;
  }

  .editable-row-operations a {
    margin-right: 8px;
  }
  .fix-basic-table {
    :deep(&.vben-basic-table .ant-table-wrapper) {
